<template>
  <BaseLayout title="礼品卡">
    <template #extra>
      <van-button icon="add" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="onAdd">开卡</van-button>
    </template>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <div v-for="(item, index) in state.list" :key="index">
          <van-tag class="dt" color="#CCC">{{item.dt}}</van-tag>
          <van-cell-group class="item" :border="false">
            <van-cell>
              <template #title>
                <div class="line1">{{item.value}}</div>
              </template>
              <template #value>
                <div class="id">#{{item.id}}</div>
                <van-icon name="gold-coin-o" size="12" @click="$copy(item.token)"> 复制卡号</van-icon>
                &nbsp;
                <van-icon name="share-o" size="12"> 分享</van-icon>
              </template>
            </van-cell>
            <van-cell class="line2" :title="item.remark" :border="false"><van-icon name="edit"> 备注</van-icon></van-cell>
          </van-cell-group>
        </div>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>
  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  text-align: center;
}

.item {
  height: 120px;
  margin: 5px;
  background: #FFF;
  border-radius: 5px;
  text-align: left;
  overflow:hidden;
}
.dt {
  margin: 10px 0 5px 0;
}
.id {
  margin-left: 20px;
  color: #999;
  font-size: 12px;
}
.line1 {
  padding-top: 10px;
  color: #0C0;
  font-size: 24px;
  font-weight: 600;
}
.line2 {
  color: #999;
  font-size: 12px;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      grid: true,
      list: [],
      offset: 0,
      limit : 20
    });
    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/team/cardList?offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push(item);
        }
      });
    },
  }
}
</script>
